@font-face {
  font-family: "PTSans";
  font-weight: 400;
  font-style: normal;
  src:
    url("../fonts/ptsans.woff") format("woff"),
    url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "PTSans";
  font-weight: 700;
  font-style: normal;
  src:
    url("../fonts/ptsansbold.woff") format("woff"),
    url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
